/* @flow */
import React from 'react';
import ReactDOM from 'react-dom';
import {BrowserRouter} from 'react-router-dom';

import App from './pages/App';

const element = (
  <BrowserRouter>
    <App />
  </BrowserRouter>
)

ReactDOM.render(
  element,
  window.document.getElementById('root')
);




